.about{
  &__inner{
    padding: 5.75em 2em;
    border-radius: 2em;
    background-color: $darkBlue;
    color: $white;
    display: grid;
    grid-template-columns: calc(100% - 27.25em) 25em;
    gap: 3.375em 2.25em;
    align-items: center;
    box-shadow: $shadow;
  }
  &__desc{
    margin-top: 1.5em;
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
}

.materials{
  &__grid{
    margin-top: 4em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .75em));
    gap: 1.25em 1.5em;
  }
}

.material-card{
  padding: 1.5em;
  background-color: $white;
  box-shadow: $shadow;
  border-radius: 2em;
  display: grid;
  grid-template-columns: 18.5em calc(100% - 20em);
  gap: 1.5em;
  align-items: center;
  &__img{
    @include imgCover(100%);
    border-radius: .5em;
  }
  &__content{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__logo{
    max-width: 17.25em;
    width: 100%;
    display: block;
  }
  &__badge{
    margin: 2em 0 .5em;
    span{
      font-size: 1.25em;
    }
  }
}

.steps{
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 4.5em / 4));
    gap: 1.5em;
    margin-top: 4em;
  }
}

.step-card{
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  &__icon{
    @include center;
    background-color: $red;
    border-radius: 1.5625em 1.5625em 1.5625em 0;
    width: 6.25em;
    height: 6.25em;
    img{
      width: 3em;
    }
  }
  &__content{
    margin-top: 1.5em;
    @include grid100gap(.5em);
  }
  &__title{
    text-transform: capitalize;
    font-weight: 600;
  }
  &__desc{
    font-size: 1.5em;
    line-height: 133%;
  }
}


@media screen and (max-width: 992px){
  .about{
    &__inner{
      padding: 2em 2.375em;
      text-align: center;
      grid-template-columns: 100%;
    }
    &__img{
      width: 24.3125em;
      margin: 0 auto;
      min-height: 12.5em;
    }
  }

  .materials{
    &__grid{
      margin-top: 2.5em;
      grid-template-columns: 100%;
      gap: 1.5em;
    }
  }

  .material-card{
    grid-template-columns: 14.1875em calc(100% - 15.6875em);
  }

  .steps{
    display: none;
  }
}


@media screen and (max-width: 576px){
  .about{
    &__inner{
      padding: 2em 1.25em 1em;
      gap: 3em;
    }
    &__img{
      width: 15.625em;
      min-height: 8em;
    }
  }

  .materials{
    &__grid{
      margin-top: 1em;
      gap: 1em;
    }
  }

  .material-card{
    border-radius: 1.5em;
    grid-template-columns: 7.5em calc(100% - 8.5em);
    gap: 1em;
    padding: .75em;
    &__logo{
      max-width: 9em;
    }
    &__badge{
      margin-top: 1em;
      height: 2em;
      padding: 0 .5em;
      span{
        font-size: .875em;
      }
    }
  }
}
