.fg {
  position: relative;
  margin: 0;
  label{
    display: block;
    font-size: .9375em;
    line-height: 140%;
    margin-bottom: .375em;
    span{
      color: $primary;
    }
  }
  .nice-select,
  input,
  textarea{
    border-radius: 0.1875em;
    border: 1px solid #DADEDF;
    padding: 0 1.25em;
    height: 3.6875em;
    width: 100%;
    font-size: 1em;
    background-color: transparent;
    color: #000;
    &::placeholder {
      color: #929798;
    }
    &:focus {
      border-color: #9CA0A0;
      box-shadow: 0 .25em .4375em 0 rgba(47, 63, 67, 0.03), 0 .25em .3125em 0 rgba(0, 0, 0, 0.02) inset;
    }
    &:disabled{
      pointer-events: none;
      background: #F6F6F6 !important;
      color: rgba(#000,.4);
    }
  }
  textarea{
    padding-top: 1.19em;
    padding-bottom: 1.19em;
    height: 13.0625em;
    resize: vertical;
  }
  &__field{
    position: relative;
  }
  .nice-select{
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 0.1875em;
    white-space: nowrap;
    height: 3.6875em;
    padding: 0 0 0 1.25em;
    &.disabled{
      pointer-events: none;
      background: #F6F6F6 !important;
      .current{
        opacity: 0.4;
      }
    }
    &.open{
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
    }
    &:focus{
      border: 1px solid $black;
    }
    &:before{
      content: '';
      order: 3;
      border-left: $black;
      flex-shrink: 0;
      width: 3.6875em;
      height: 3.6875em;
      background-image: url(../img/select-arrow.svg);
      @include bgImgSize(.5em);
      margin-left: 1em;
    }
    .current{
      font-size: 1em;
      line-height: 157%;
    }
    .list{
      margin-top: -1px;
      border-radius: 0 0 .1875em .1875em;
      width: calc(100% + 2px);
      left: -1px;
      border: 1px solid $black;
      background: #FFF;
      box-shadow: none;
      max-height: 14.875em;
      overflow-y: auto;
      z-index: 5;
      @include scroll;
    }
    .option{
      height: 3.6875em;
      font-size: 1em;
      line-height: 157%;
      padding: .5em 1.25em;
      display: flex;
      align-items: center;
      color: #2B2B2B;
      border: none;
      border-bottom: 1px solid $black;
      &:last-child{
        border-bottom: none;
      }
      &.disabled{
        display: none;
      }
      &:hover,
      &.focus,
      &.selected.focus{
        background-color: $primary;
        border-color: $primary;
        font-weight: 400;
        color: #fff;
      }
    }
  }
  &._error{
    input,
    textarea{
      border-color: $primary;
      color: $primary;
      &::placeholder{
        color: $primary;
      }
    }
    .fg__error-text{
      display: block;
    }
  }
  &__error-text{
    margin-top: .375em;
    color: $primary;
    display: none;
  }
}

.rating-block{
  display: flex;
  align-items: center;
  &__num{
    display: flex;
    align-items: center;
    order: -1;
    font-size: 25px;
    font-weight: 700;
    margin-right: .33em;
  }
}

.rating-block{
  &._disabled{
    pointer-events: none;
  }
}

.rating{
  cursor: pointer;
  &__stars{
    width: 7.375em;
    max-width: 100%;
    position: relative;
    img{
      width: 100%;
      display: block;
    }
  }
  &__fill{
    width: 0;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    overflow: hidden;
    img{
      width: 7.375em;
      display: block;
    }
  }
}
