.want{
  .container{
    display: flex;
  }
  &__inner{
    width: 100%;
    margin-top: 9em;
    padding: 3.125em 2em;
    background-color: $red;
    border-radius: 3em;
    color: $white;
    min-height: 26.25em;
    position: relative;
  }
  &__img{
    position: absolute;
    bottom: 2.25em;
    right: 5.75em;
    width: 44.625em;
  }
  &__content{
    max-width: 28.125em;
  }
  &__desc{
    margin-top: 1em;
  }
  &__nav{
    margin-top: 2em;
    display: flex;
  }
}

.offer{
  grid-column: span 2;
  padding: 4.875em 3em;
  border-radius: 2em;
  background-color: $darkBlue;
  color: $white;
  box-shadow: $shadow;
  position: relative;
  overflow: hidden;
  &__img{
    position: absolute;
    right: 0;
    bottom: 0;
    width: 33.4375em;
    pointer-events: none;
  }
  &__list{
    margin-top: 3em;
    max-width: 23.75em;
    padding-left: .8125em;
    @include grid100gap(1em);
    li{
      font-size: 1.5em;
      line-height: 120%;
      position: relative;
      padding-left: 1em;
      &:before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
      }
    }
  }
  &__nav{
    display: flex;
    margin-top: 2.375em;
  }
  &__btn{
    width: 20.625em;
    max-width: 100%;
  }
}


@media screen and (max-width: 992px){
  .want{
    padding: 1.25em 0;
      &__inner{
      margin: 0;
      padding: 2.5em 2em 1.25em;
      text-align: center;
    }
    &__content{
      max-width: unset;
    }
    &__img{
      position: static;
      display: block;
      margin: 1.75em auto 0;
      width: 37em;
      max-width: 100%;
    }
    &__nav{
      margin-top: 1em;
      justify-content: center;
    }
    &__btn{
      height: 3em;
      padding: 0 .75em;
    }
  }

  .catalog2{
    padding-top: 1.25em;
  }

  .offer{
    grid-column: unset;
    padding: 15.9375em 1.5em 1.5em;
    &__nav{
      display: none;
    }
    &__title{
      font-size: 2em;
    }
    &__list{
      margin-top: 1em;
    }
    &__img{
      width: 21.75em;
      right: -3em;
      top: -6.25em;
      bottom: unset;
    }
  }
}


@media screen and (max-width: 576px){
  .want{
    &__inner{
      padding: 1.6875em 1.25em 1.5em;
      border-radius: 2em;
    }
    &__title{
      font-size: 2.25em;
    }
    &__btn{
      padding: 0 1.125em;
    }
    &__img{
      width: calc(100% + 1em);
      max-width: unset;
      margin: 2em 0 0 -.5em;
    }
  }

  .offer{
    grid-column: 1/-1;
    padding: 19.375em 1.5em 1.625em;
    &__title{
      font-size: 1.5em;
    }
    &__list{
      padding-left: .5em;
      li{
        font-size: 1em;
      }
    }
    &__img{
      width: 24.75em;
      right: 50%;
      transform: translateX(46%);
      top: -5.25em;
    }
  }
}
