@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 2em;
    height: 4.5em;
    border-radius: 2.25em;
    font-weight: 400;
    font-family: $font1;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1.5em;
        line-height: 150%;
    }
    img{
        width: 1.5em;
        margin-left: .6875em;
    }
    &--small{
        height: 3.5em;
        padding: 0 1em;
        span{
            font-size: 1.25em;
        }
    }
}


.m-btn-green{
    background-color: $primary;
    color: $white;
    &:hover{
        background-color: darken($primary,10%);
        color: $white;
    }
}


.m-btn-white{
    background-color: $white;
    color: $darkBlue;
    &:hover{
        background-color: darken($white,10%);
        color: $darkBlue;
    }
}


@media screen and (max-width: 992px){
    .m-btn{
        height: 4em;
        span{
            font-size: 1.25em;
        }
    }
}


@media screen and (max-width: 576px){
    .m-btn{
        height: 3em;
        span{
            font-size: 1em;
        }
    }
}
