.tabs{
  display: flex;
}

.tab{
  height: 2.5em;
  padding: 0  .625em;
  @include center;
  transition: all .3s;
  border-radius: 1.5em;
  span{
    font-size: 1.5em;
    line-height: 120%;
  }
  &:hover,
  &._active{
    background-color: $red;
    color: $white;
  }
  &:hover{
    cursor: pointer;
  }
  &._active{
    pointer-events: none;
  }
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}


@media screen and (max-width: 576px){
  .tab{
    span{
      font-size: 1.25em;
    }
  }
}
