.footer{
  margin-top: 4.6875em;
  padding: 6.25em 0;
  &__inner{
    padding: 0 8.25em;
  }
  &__logo{
    display: block;
    width: 7.8125em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__grid{
    display: flex;
    justify-content: space-between;
    margin-top: 2.5em;
  }
  &__wrap{
    display: flex;
    justify-content: space-between;
    flex-grow: 1;
  }
  &__bottom{
    margin-top: 2.5em;
    padding-top: 2.1875em;
    border-top: 1px solid rgba($darkBlue,.06);
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.footer-contacts{
  margin-right: 5em;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  &__link{
    @include lastM(0 0 .625em);
    white-space: nowrap;
    span{
      font-size: 1.5em;
      line-height: 133%;
    }
  }
}

.footer-nav{
  flex-grow: 1;
  max-width: 20.5em;
  column-count: 2;
  column-gap: 2em;
  &__link{
    margin-bottom: 2.375em;
    color: rgba($darkBlue,.65);
    display: inline-block;
    width: 100%;
    span{
      font-size: 1.25em;
      line-height: 120%;
    }
  }
}

.footer-subscribe{
  width: 20.5em;
  text-align: center;
  &__title{
    font-size: 1.5em;
    line-height: 117%;
  }
  &__field{
    margin-top: 1.5em;
    position: relative;
    input{
      width: 100%;
      height: 3.125em;
      border-radius: .375em;
      padding: 0 4em 0 1em;
      font-size: 1em;
      font-family: $font1;
      border: 1px solid $primary;
      &::placeholder{
        color: rgba($darkBlue,.65);
      }
    }
  }
  &__btn{
    position: absolute;
    height: 3.125em;
    width: 3.125em;
    border-radius: 0 .375em .375em 0;
    padding: 0;
    top: 0;
    right: 0;
    img{
      margin: 0;
      width: 1.1875em;
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    margin-top: 3.75em;
    padding: 5.875em 0 3.75em;
    &__inner{
      padding: 0 1.5em;
    }
    &__wrap{
      display: block;
    }
    &__bottom{
      margin-top: 3em;
      padding-top: 2.5em;
    }
  }

  .footer-contacts{
    margin-right: 3.75em;
    &__link{
      @include lastM(0 0 .5em);
      span{
        font-size: 1.25em;
        line-height: 160%;
      }
    }
  }

  .footer-nav{
    max-width: unset;
    @media screen and (min-width: 577px){
      column-count: unset;
      display: flex;
      align-items: center;
      column-gap: unset;
      &__link{
        margin: 0 2em 0 0;
        &:last-child{
          margin-right: 0;
        }
      }
    }
  }

  .footer-subscribe{
    margin-top: 1em;
    text-align: left;
    &__title{
      font-size: 1.25em;
    }
    &__field{
      margin-top: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    margin-top: 3em;
    &__inner{
      padding: 0;
    }
    &__grid{
      margin-top: 2em;
      display: block;
    }
  }

  .footer-contacts{
    margin: 0 0 1.5em;
  }

  .footer-nav{
    &__link{
      margin-bottom: 1em;
    }
  }

  .footer-subscribe{
    width: unset;
  }
}
