.calc,
.order{
  &__nav{
    margin-top: auto;
    display: flex;
  }
  &__btn{
    width: 20.625em;
  }
}

.order{
  padding: 2.5em 2em;
  background-color: $primary;
  color: $white;
  border-radius: 3em;
  grid-column: 1/-1;
  display: grid;
  grid-template-columns: 37em calc(100% - 39.5em);
  gap: 2.5em;
  &__content{
    display: flex;
    flex-direction: column;
  }
  &__title{
    br{
      display: none;
    }
  }
  &__desc{
    max-width: 28.125em;
    margin-top: 1em;
  }
  &__nav{
    padding-top: 2.5em;
  }
  &__img{
    img{
      display: block;
      width: 100%;
      border-radius: .5em;
    }
  }
}

.calc{
  padding: 3em;
  grid-column: span 2;
  border-radius: 2em;
  background-color: $darkBlue;
  color: $white;
  box-shadow: $shadow;
  position: relative;
  overflow: hidden;
  display: flex;
  align-items: center;
  &__content{
    max-width: 26.875em;
  }
  &__desc{
    margin-top: 2em;
    b{
      display: block;
    }
  }
  &__nav{
    margin-top: 2em;
  }
  &__img{
    position: absolute;
    top: .62em;
    right: 0;
    width: 28em;
    transform: rotate(17deg);
    mix-blend-mode: difference;
  }
}

.guarantee{
  padding: 2.375em 3em;
  background-color: $primary;
  border-radius: 2em;
  box-shadow: $shadow;
  color: $white;
  grid-column: span 2;
  display: flex;
  flex-direction: column;
  &__content{
    max-width: 32.25em;
    @include grid100gap(1.375em);
    margin-bottom: 2em;
  }
  &__title{
    font-size: 3em;
    line-height: 120%;
    font-weight: 600;
  }
  &__img{
    margin: auto -3em -2.375em auto;
    width: 36.25em;
    display: block;
  }
}


@media screen and (max-width: 992px){
  .order{
    grid-template-columns: 100%;
    text-align: center;
    &__desc{
      margin-left: auto;
      margin-right: auto;
    }
    &__nav{
      justify-content: center;
    }
    &__btn{
      height: 4em;
      width: 19.375em;
    }
  }

  .calc{
    grid-column: unset;
    display: block;
    padding: 0 1.5em 1.5em;
    &__img{
      position: static;
      width: 12.5em;
      display: block;
      margin: 0 auto;
      transform: rotate(37deg);
    }
    &__title{
      font-size: 2em;
    }
    &__desc{
      .text32{
        font-size: 1.5em;
        b{
          margin-bottom: .75em;
        }
      }
    }
    &__nav{
      margin-top: 1.875em;
    }
    &__btn{
      width: 100%;
      height: 4em;
    }
  }

  .guarantee{
    padding: 1.6875em 1.5em 1.875em;
    grid-column: unset;
    &__content{
      gap: 1em;
      margin: 0 0 1em;
    }
    &__title{
      font-size: 2em;
    }
    &__img{
      width: calc(100% + 3em);
      margin: auto 0 0 -1.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .order{
    padding: 1.5em;
    border-radius: 2em;
    gap: 1.5em;
    &__title{
      font-size: 1.5em;
      br{
        display: unset;
      }
    }
    &__desc{
      margin-top: .5em;
    }
    &__nav{
      padding-top: 1em;
    }
    &__btn{
      width: 13.5em;
      height: 3em;
    }
    &__img{
      @include imgCover(102%);
      border-radius: .5em;
    }
  }

  .calc{
    padding-bottom: 3em;
    text-align: center;
    grid-column: 1/-1 ;
    &__desc{
      margin-top: 2.25em;
      .text32{
        font-size: 1.25em;
      }
    }
    &__nav{
      margin-top: 2.625em;
      justify-content: center;
    }
    &__btn{
      width: 12.5em;
      height: 3em;
      span{
        font-size: 1em;
      }
    }
  }

  .guarantee{
    padding: 1.5em;
    grid-column: 1/-1;
    &__content{
      gap: 1.25em;
      margin: 0 0 .75em;
    }
    &__title{
      font-size: 1.5em;
    }
    &__img{
      width: 100%;
      margin: 0;
    }
  }
}
