.m-section{
  padding: 4.6875em 0;
}

.m-title2{
  text-align: center;
  span{
    position: relative;
    &:before{
      content: '';
      position: absolute;
      bottom: 0;
      right: 0;
      height: 2px;
      width: 100%;
      background-color: $primary;
    }
  }
}
.m-title{
  display: flex;
  justify-content: center;
  text-align: center;
  .h2{
    position: relative;
    &:before{
      content: '';
      position: absolute;
      bottom: 0;
      right: 0;
      height: 2px;
      width: 2.025em;
      background-color: $primary;
    }
  }
}

.tabs{
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3 - 1em));
  gap: 1.5em;
  width: 100%;
  &__wrap{
    margin: 1.875em auto 0;
    width: 58.125em;
    max-width: 100%;
    display: flex;
    justify-content: center;
    padding: .25em;
    border-radius: 1.5em;
    border: 1px solid $red;
  }
}

.catalog{
  .tab-content{
    margin-top: 2.5em;
  }
}

.catalog-items{
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
  gap: 1.25em;
  grid-auto-flow: dense;
}

.product-card{
  box-shadow: $shadow;
  background-color: $white;
  border-radius: 2em;
  padding: 1.5em;
  display: flex;
  flex-direction: column;
  &__img{
    @include imgCover(76.2%);
    border-radius: .5em;
  }
  &__top{
    margin-top: 1.5em;
    display: flex;
    align-items: center;
  }
  &__title{
    font-size: 2em;
  }
  &__sale{
    margin-left: .5em;
  }
  &__props{
    margin-top: 1.5em;
    @include grid100gap(.25em);
  }
  &__prop{
    font-size: 1.25em;
    line-height: 120%;
    span{
      color: $red;
    }
  }
  &__bottom{
    margin-top: auto;
    padding-top: 2em;
  }
  &__prices{
    display: flex;
    align-items: center;
    margin-top: .25em;
  }
  &__price{
    font-size: 2.5em;
    line-height: 120%;
  }
  &__price2{
    font-size: 1.25em;
    line-height: 120%;
    opacity: .5;
    margin-left: .75em;
  }
  &__btn{
    margin-top: 1.25em;
    height: 3em;
    span{
      font-size: 1.25em;
      line-height: 120%;
    }
  }
}

.product-slider{
  overflow: hidden;
  border-radius: .5em;
  position: relative;
}

.swiper-button{
  width: 2em;
  height: 2em;
  @include center;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: rgba(28, 44, 44, 0.4);
    backdrop-filter: blur(16px);
  }
  svg{
    width: 1.25em;
    position: relative;
    z-index: 2;
  }
  &.swiper-button-disabled{
    opacity: 0;
  }
}

.m-badge{
  @include center;
  padding: 0 .5em;
  color: $white;
  background-color: $red;
  height: 2em;
  border-radius: .5em;
  span{
    font-size: 1em;
  }
}


@media screen and (max-width: 992px){
  .m-section{
    padding: 3.75em 0;
  }

  .tabs{
    &__wrap{
      width: 40em;
    }
  }

  .catalog{
    padding-bottom: 1.25em;
  }

  .catalog-items{
    grid-template-columns: repeat(2,calc(50% - .625em));
  }

  .product-card{
    &__img{
      padding-bottom: 101%;
    }
    &__btn{
      height: 3.5em;
    }
  }

  .product-slider{
    .swiper-button{
      display: none;
    }
  }
}


@media screen and (max-width: 576px){
  .m-section{
    padding: 3em 0;
  }

  .tabs{
    grid-template-columns: 1fr 1fr 1.2fr;
    gap: 1em;
  }

  .catalog-items{
    grid-template-columns: repeat(2,calc(50% - .5em));
    gap: 1.25em 1em;
  }

  .product-card{
    padding: .75em;
    border-radius: 1em;
    &__img{
      padding-bottom: 61.7%;
    }
    &__top{
      margin-top: 1em;
      flex-direction: column;
      align-items: flex-start;
    }
    &__sale{
      order: -1;
      margin: 0 0 .25em;
    }
    &__title{
      font-size: 1.125em;
    }
    &__props{
      margin-top: 1em;
    }
    &__prop{
      font-size: .75em;
    }
    &__bottom{
      padding-top: 1em;
    }
    &__price{
      font-size: 1.5em;
    }
    &__price2{
      font-size: .875em;
      margin-left: .57em;
    }
    &__btn{
      height: 3em;
      padding: 0 .5em;
      span{
        font-size: .75em;
      }
    }
  }

  .m-badge{
    height: 1.5625em;
    padding: 0 .25em;
    span{
      font-size: .875em;
    }
  }
}
