.stay-home{
  &__inner{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__btn{
    margin-top: 3em;
    height: 4em;
    width: 21em;
    max-width: 100%;
    span{
      font-size: 1.25em;
    }
  }
  &__desc{
    margin-top: 1.5em;
    span{
      color: $primary;
    }
  }
}

.reviews{
  overflow: hidden;
}

.reviews-slider{
  padding: 0 7.125em;
  position: relative;
  .swiper{
    &-container{
      width: calc(100% + 1.25em);
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      padding-right: 1.25em;
      width: 50%;
      height: auto;
      @media screen and (min-width: 993px){
        transition: all .3s;
        opacity: 0;
        pointer-events: none;
        &-active,
        &-next{
          opacity: 1;
          pointer-events: unset;
        }
      }
    }
    &-button{
      margin: 0;
      top: 50%;
      transform: translateY(-50%);
      &:before{
        background-color: $darkBlue;
      }
      &-prev{
        left: 3.875em;
      }
      &-next{
        right: 3.875em;
      }
    }
  }
}

.review-card{
  padding: 3em;
  background-color: $white;
  box-shadow: $shadow;
  height: 100%;
  display: flex;
  flex-direction: column;
  border-radius: 2em;
  &__text{
    font-size: 1.1875em;
    line-height: 150%;
    letter-spacing: 0.04em;
  }
  &__bottom{
    margin-top: auto;
    padding-top: 2em;
    display: flex;
    align-items: center;
  }
  &__name{
    font-size: 1.0625em;
    letter-spacing: 0.04em;
    font-weight: 700;
  }
  .rating-block{
    margin-right: .75em;
    &__num{
      display: none;
    }
  }
}


@media screen and (max-width: 992px){
  .stay-home{
    &__btn{
      margin-top: 2em;
      width: 19.375em;
    }
    &__desc{
      margin-top: 1em;
    }
  }

  .reviews-slider{
    padding: 0 4.375em;
    .swiper{
      &-slide{
        width: 100%;
      }
      &-button{
        &-prev{
          left: 1.125em;
        }
        &-next{
          right: 1.125em;
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .stay-home{
    &__btn{
      margin-top: 1.5em;
      width: auto;
      padding: 0 3em;
      height: 3em;
      span{
        font-size: 1em;
      }
    }
    &__desc{
      margin-top: .5em;
    }
  }

  .reviews-slider{
    padding: 0 3.125em;
    .swiper{
      &-container{
        width: calc(100% + 1em);
      }
      &-slide{
        padding-right: 1em;
      }
      &-button{
        &-prev{
          left: .125em;
        }
        &-next{
          right: .125em;
        }
      }
    }
  }

  .review-card{
    padding: .75em;
    border-radius: 1.5em;
    &__text{
      font-size: .875em;
      line-height: 157%;
    }
    &__bottom{
      padding-top: .5em;
    }
  }
}
