.header{
  padding-top: 6.25em;
  position: relative;
  z-index: 10;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo{
    display: block;
    flex-shrink: 0;
    width: 6em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__phone{
    svg{
      width: 1.5em;
      margin-right: .5em;
    }
    &:hover{
      path{
        fill: $primary;
      }
    }
  }
}

.menu{
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-grow: 1;
  max-width: 64.625em;
}

.nav{
  display: flex;
  &__item:hover .nav__show,
  &__link:hover{
    span{
      color: $primary;
      border-bottom-color: $primary;
    }
  }
  &__link,
  &__show{
    transition: all .3s;
    display: flex;
    align-items: center;
    span{
      font-size: 1.125em;
      line-height: 122%;
      transition: inherit;
      padding: 1px 0;
      border-top: 1px solid transparent;
      border-bottom: 1px solid transparent;
    }
  }
  &__show{
    svg{
      margin-left: .5em;
      width: 1.3125em;
    }
    &:hover{
      cursor: pointer;
    }
  }
  &__item,
  &__link{
    margin-right: 2em;
  }
  &__sub-link{
    white-space: nowrap;
    display: block;
    &:not(:last-child){
      margin-bottom: .5em;
      padding-bottom: .5em;
      border-bottom: 1px solid #d9d9d9;
    }
    span {
      font-size: 1.125em;
      line-height: 122%;
    }
  }
  @media screen and (min-width: 993px){
    &__item{
      position: relative;
      &:hover{
        .nav__show{
          svg{
            transform: rotate(-180deg);
          }
          path{
            stroke: $primary;
          }
        }
        .nav__hidden{
          opacity: 1;
          pointer-events: unset;
        }
      }
    }
    &__hidden{
      position: absolute;
      top: 100%;
      right: -.375em;
      margin-top: -1px;
      padding-top: .6875em;
      transition: all .3s;
      opacity: 0;
      pointer-events: none;
    }
    &__inner{
      padding: 1em;
      box-shadow: $shadow;
      background-color: $white;
      border-radius: 1em;
    }
  }
}

._flex{
  display: flex;
  align-items: center;
}

.header-search{
  position: relative;
  margin-left: auto;
  margin-right: 3.125em;
  &__show{
    width: 1.5em;
    @include center;
    svg{
      width: 100%;
    }
    &:hover{
      path{
        fill: $primary;
      }
    }
  }
  &__close,
  &__hidden,
  &__icon{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
  }
  &__icon{
    left: .5em;
    width: 1.5em;
  }
  &__close{
    right: .625em;
    &:hover{
      g{
        opacity: 1;
      }
    }
  }
  &__hidden{
    right: -1.5em;
    width: 52.125em;
    @media screen and (min-width: 993px){
      opacity: 0;
      pointer-events: none;
      transition: all .3s;
    }
  }
  &__field{
    input{
      height: 2.5em;
      width: 100%;
      font-size: 1em;
      line-height: 120%;
      padding: 0 2.625em;
      border-radius: 1.25em;
      background: #f6f6f6;
      border: none;
      font-family: $font1;
      &:focus{
        background: darken(#f6f6f6,2%);
      }
      &::placeholder{
        color: rgba(#000,.2);
      }
    }
  }
  &._open{
    .header-search__hidden{
      opacity: 1;
      pointer-events: unset;
    }
  }
}

.burger{
  display: none;
}


@media screen and (max-width: 992px){
  .header{
    padding-top: 1.5em;
    &__logo{
      position: relative;
      z-index: 12;
    }
    &__phone{
      margin-top: 3em;
    }
    &._open{
      .menu{
        opacity: 1;
        pointer-events: unset;
      }
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
    }
  }

  .menu{
    flex-direction: column;
    align-items: flex-start;
    justify-content: unset;
    position: fixed;
    padding: 5.375em 1.5em 0;
    width: 100%;
    height: $vh;
    max-width: unset;
    top: 0;
    left: 0;
    background-color: $white;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
  }

  .nav{
    flex-direction: column;
    align-items: flex-start;
    &__link,
    &__item{
      margin: 0 0 1em;
    }
    &__hidden{
      padding: 0;
    }
    &__inner{
      display: flex;
      flex-direction: column;
      align-items: flex-start;
    }
    &__sub-link{
      border: none !important;
    }
    &__show{
      display: none;
    }
  }

  .burger{
    z-index: 11;
    position: relative;
    width: 1.5em;
    height: 1.5em;
    display: flex;
    img{
      @include centerAbsolute;
      width: 100%;
      transition: inherit;
    }
    ._view2{
      opacity: 0;
      width: 80%;
    }
  }

  .header-search{
    order: -1;
    margin: 0 0 1.5em;
    width: 100%;
    &__close,
    &__show{
      display: none;
    }
    &__hidden{
      transform: unset;
      position: static;
      width: 100%;
    }
  }
}


