.main{
  padding: 1.5em 0 4.6875em;
  &__inner{
    padding: 5.5em 2em 3.875em;
    border-radius: 3em;
    min-height: 47.5em;
    display: flex;
    flex-direction: column;
    position: relative;
  }
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: inherit;
    z-index: -1;
    &:after{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(61deg, rgba(255, 251, 244, 0.7) 0%, rgba(255, 251, 244, 0) 100%);
      pointer-events: none;
      border-radius: inherit;
      opacity: .8;
    }
    img{
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: inherit;
    }
    ._view2,
    ._view3{
      display: none;
    }
  }
  &__place{
    display: flex;
    align-items: center;
    color: $red;
    margin-bottom: 1em;
    span{
      font-size: 1.25em;
    }
    img{
      width: 1.5em;
      margin-right: .5em;
    }
  }
  &__nav{
    display: flex;
    margin-top: 3em;
  }
  &__bottom{
    margin-top: auto;
    padding: 3em;
    position: relative;
    border: 2px solid $white;
    overflow: hidden;
    border-radius: 1.5em;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      backdrop-filter: blur(16px);
      background: rgba(253, 253, 253, 0.4);
      border-radius: inherit;
    }
  }
}

.advantages{
  position: relative;
  z-index: 2;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.advantage-item{
  display: flex;
  align-items: center;
  max-width: 18em;
  img{
    width: 2em;
    flex-shrink: 0;
    margin-right: 1em;
  }
  span{
    font-size: 1.5em;
    line-height: 120%;
  }
}

.lh103{
  line-height: 103%;
}


@media screen and (max-width: 992px){
  .main{
    padding-bottom: 3.75em;
    &__inner{
      padding: 1.5em 1.5em 0;
      min-height: 36.0625em;
    }
    &__top{
      max-width: 23.375em;
      @include grid100gap(.5em);
    }
    &__place{
      margin-bottom: .5em;
    }
    .h1{
      line-height: 110%;
    }
    .h3{
      max-width: 11.7em;
    }
    &__nav{
      margin-top: 2em;
    }
    &__img{
      &:after{
        opacity: 1;
      }
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__bottom{
      padding: 2em 2.875em;
      transform: translateY(50%);
    }
  }

  ._hide-tablet{
    display: none;
  }

  .advantages{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .9375em));
    gap: .5em 1.875em;
  }

  .advantage-item{
    span{
      font-size: 1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    padding-bottom: 15.75em;
    &__inner{
      padding: 1.5em 1.5em 6.25em;
    }
    .h1{
      max-width: 8.3em;
    }
    &__place{
      span{
        font-size: 1em;
      }
    }
    &__img{
      ._view2{
        display: none;
      }
      ._view3{
        display: block;
      }
    }
    &__nav{
      margin-top: auto;
    }
    &__bottom{
      width: calc(100% - 3em);
      transform: translate(-50%,12.5em);
      padding: 2em 1.5em;
      position: absolute;
      bottom: 0;
      left: 50%;
    }
  }

  .advantages{
    grid-template-columns: 100%;
    gap: 1.5em;
  }

  .advantage-item{
    img{
      width: 1.5em;
      margin-right: .75em;
    }
  }
}
